@import url(https://fonts.googleapis.com/css?family=Nunito+Sans);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* A.J. Bale's marriage of:;
   Reset CSS | v2.0 | http://meyerweb.com/eric/tools/css/reset/
   &&
   normalize.css | v8.0.0 | https://necolas.github.io/normalize.css/
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video,
button, input, optgroup, select, textarea {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer,
header, hgroup, main, menu, nav, section, summary {
    display: block;
}
body {
    line-height: 1;
}
a {
    background-color: transparent;
}
a:active,
a:hover {
    outline: 0;
}
b,
strong {
    font-weight: bold;
}
code,
kbd,
samp {
    font-family: monospace, monospace;
    font-size: 1em;
}
img {
    border-style: none;
}
ol, ul {
    list-style: none;
}
button,
input {
    overflow: visible;
}
button,
[type="button"],
[type="reset"],
[type="submit"] {
    -webkit-appearance: button;
}
button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
    border-style: none;
    padding: 0;
}
button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
    outline: 1px dotted ButtonText;
}
textarea {
  overflow: auto;
}
blockquote, q {
    quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
    content: '';
    content: none;
}
table {
    border-collapse: collapse;
    border-spacing: 0;
}
.flex {
    display: flex;
    flex: 0 1 auto;
}
.App {
  text-align: center;
  font-family: 'Nunito Sans', sans-serif;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.hero-slider {
  z-index: 75;
}
.hero-slider div {
  height: 100vh;
}
.slick-slide div div {
  background-position: top center;
  background-repeat: no-repeat;
  background-size: cover;
}
.hero-slider div#one {
  background-image: url(/react_mega_menu/static/media/HOMEPAGE_SLIDER_IMAGE.88f4473e.jpg);
}
.hero-slider div#two {
  background-image: url(/react_mega_menu/static/media/HOMEPAGE_SLIDER_IMAGE_FLIPPED.666aacb1.jpg);
}
#main-navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 4em;
  padding: 0 1em;
  width: 100%;
  color: #000;
  background-color: rgba(255,255,255,1);
  -webkit-transition: all 0.4s ease;
  transition: all 0.4s ease;
}

#main-navigation div {
  -webkit-transition: all 0.4s ease;
  transition: all 0.4s ease;
}

#main-navigation a {
  color: #000;
  text-decoration: none;
  -webkit-transition: all 0.4s ease;
  transition: all 0.4s ease;
}

.hamburger-menu {
  height: 1.25em;
  width: 25%;
  background-image: url(/react_mega_menu/static/media/menu.c9940670.svg);
  background-position: left center;
  background-repeat: no-repeat;
  background-size: contain;
}

.logo {
  height: 2em;
  width: 25%;
  max-width: 140px;
  background-image: url(/react_mega_menu/static/media/logo_black.8386e2f2.png);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: contain;
}

#PrimaryNav {
  display: none;
}

.header-tools {
  width: 25%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-tools div {
  height: 1.25em;
  min-width: 1em;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: contain;
}

.header-tools a {
  opacity: 0;
}

.account {
  background-image: url(/react_mega_menu/static/media/profile.4b7620c7.svg);
}

.search {
  background-image: url(/react_mega_menu/static/media/icon_search.52733500.svg);
}

.cart {
  background-image: url(/react_mega_menu/static/media/shopping-bag.03d94706.svg);
}
@media (min-width: 450px) {
  .hamburger-menu,
  .header-tools {
    width: 15%;
  }
  .header-tools div {
    min-width: 2em;
  }

}
@media (min-width: 991px) {
  #main-navigation {
    position: fixed;
    top: 0;
    height: 5em;
    color: #fff;
    background-color: rgba(255,255,255,0);
    z-index: 999;
  }
  #main-navigation:hover {
    background-color: rgba(255,255,255,1);
    color: #000;
  }
  #main-navigation a {
    color: #fff;
  }
  #main-navigation:hover a {
    color: #000;
  }
  .hamburger-menu {
    display: none;
  }
  .logo {
    height: 3em;
    flex: 1 1;
    max-width: none;
    background-image: url(/react_mega_menu/static/media/logo_white.8078e52f.png);
    background-position-x: left;
  }
  #main-navigation:hover .logo {
    background-image: url(/react_mega_menu/static/media/logo_black.8386e2f2.png);
  }
  #PrimaryNav {
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 4.8 1;
    padding: 0 1em;
  }
  .header-tools {
    flex: 1 1;
    margin-left: 1em;
  }
  .header-tools a {
    opacity: 1;
  }
  .account,
  .cart {
    background: none;
  }
  .search:hover,
  .account a:hover,
  .cart a:hover {
    cursor: pointer;
    opacity: 0.5;
  }
  #main-navigation .search {
    background-image: url(/react_mega_menu/static/media/icon_search_white.c0bb1919.svg);
  }
  #main-navigation:hover .search {
    background-image: url(/react_mega_menu/static/media/icon_search.52733500.svg);
  }
}
.menu-dropdown {
  display: flex;
  justify-content: center;
  pointer-events: none;
  opacity: 0;
  z-index: 1;
}

@media( min-width: 911px ) {
  .menu-dropdown {
    position: absolute;
    padding: 0 0 1em;
    width: 100%;
    background-color: #fff;
    left: 0;
  }

  .menu-item.has-dropdown:hover + .menu-dropdown {
    opacity: 1;
    z-index: 99;
    pointer-events: visible;
  }

  .menu-dropdown:hover {
    opacity: 1;
    z-index: 99;
    pointer-events: visible;
  }

  .menu-dropdown div:not(:last-child) {
    margin-right: 2em;
  }

  .menu-subcategory-section {
    text-align: left;
  }
  
  .menu-subcategory {
    display: block;
    color: #000 !important;
    margin-bottom: 1em;
  }
  
  .menu-featured-wrap {
    width: 16%;
  }

  .menu-featured-img {
    width: 100%;
  }
  
  .menu-featured-label {
    color: #000;
    text-align: left;
    text-transform: uppercase;
  }
}

.menu-item {
  text-transform: uppercase;
}

@media( min-width: 911px ) {
  .menu-item-wrap {
    height: 100%;
  }
  .menu-item {
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 1em;
  }

  .menu-item span {
    border-top: 1px solid transparent;
    border-bottom: 1px solid transparent;
  }
  
  .menu-item-wrap:hover span {
    border-bottom: 1px solid #000;
  }
}

#PrimaryNav {
  height: 100%;
}
@media( min-width: 911px ) {
  .search-panel-form {
    position: absolute;
  }

  .search-panel-form input[type="text"] {
    width: 30em;
    padding: 1em 0.5em;
    margin-right: 0.5em;
    border-bottom: 2px solid #333;
  }
  .search-panel-form input[type="text"]::-webkit-input-placeholder {
    color: #333;
  }
  .search-panel-form input[type="text"]::-moz-placeholder {
    color: #333;
  }
  .search-panel-form input[type="text"]::-ms-input-placeholder {
    color: #333;
  }
  .search-panel-form input[type="text"]::placeholder {
    color: #333;
  }

  .search-panel-form input[type="submit"] {
    padding: calc(1em + 4px) 2em;
    border-radius: 3px;
    background-color: #333;
    color: #fff;
    box-shadow: 0 1px 2px rgba(0,0,0,.45);
    -webkit-transition: all 0.2s ease;
    transition: all 0.2s ease;
  }

  .search-panel-form input[type="submit"]:hover {
    cursor: pointer;
    background-color: #000;
    box-shadow: none;
  }
}

@media( min-width: 911px ) {
  .search-panel {
    position: fixed;
    top: 0;
    height: 100vh;
    width: 100%;
    background-color: #fff;
    padding: 2em;
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
    z-index: 9999;
  }

  .search-panel.collapsed {
    top: -6em;
    height: 0;
  }

  .search-panel-exit {
    position: absolute;
    right: 2em;
    width: 2em;
    height: 2em;
    background-image: url(/react_mega_menu/static/media/close.c4c396d3.svg);
    background-position: center center;
    background-repeat: no-repeat;
    background-size: contain;
  }

  .search-panel-exit:hover {
    cursor: pointer;
  }
}

